import React from 'react';
import { List, InputItem } from 'antd-mobile';
import {to_fixed2} from "../../../../util/util"
import rmb from '../../img/rmb@2x.png'
import './css.css'



class HomeIndex extends React.Component{
  constructor(props) {
    super(props);
    this.state={

    }
  }
  componentDidMount() {

  }
  clearNoNum (obj) {
    let self_ = this;
    obj = obj.replace(/[^\d.]/g, ""); //"""."
    obj = obj.replace(/^\./g, ""); //
    obj = obj.replace(/\.{2,}/g, "."); //. 
    obj = obj.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
    obj = obj.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //2
    if (obj.indexOf(".") < 0 && obj != "") {
      if (obj.substr(0, 1) == '0' && obj.length == 2) {
        obj = obj.substr(1, obj.length);
      }
    }
    return obj
  }
  set_price1(e){
    var a=  this.clearNoNum(e)
    this.props.set_price(a)
  }
  set_mobile1(e){
    this.props.set_mobile(e)
  }
  to_fix2(e){
    if(e!=''){
      this.props.set_price(to_fixed2(e))
    }
  }
  render() {
    return (
      <div className="cash_withdrawals_form">
        <List renderHeader={() => '(  : )'}>
          <InputItem
            placeholder=""
            value={this.props.price}
            type="money"
            moneyKeyboardAlign="left"
            onChange={this.set_price1.bind(this)}
            onBlur={this.to_fix2.bind(this)}
          ><img src={rmb} alt="" className="rmb"/></InputItem>
        </List>
        <List renderHeader={() => ''}>
          <InputItem
            placeholder=""
            value={this.props.mobile}
            type="phone"
            moneyKeyboardAlign="left"
            onChange={this.set_mobile1.bind(this)}
          />
          {/*<InputItem*/}
            {/*type="phone"*/}
            {/*placeholder=""*/}
            {/*error={this.state.hasError}*/}
            {/*onErrorClick={this.onErrorClick}*/}
            {/*onChange={this.set_mobile1.bind(this)}*/}
            {/*value={this.props.mobile}*/}
          {/*>*/}
        </List>
        <div className="total_cash">{this.props.balance}</div>

      </div>
    );
  }
}

export default HomeIndex




