import React from 'react'
import css from './css.css'
import btn from './img/btn@2x.png'
import { inject, observer } from 'mobx-react';
import {Toast,Modal} from 'antd-mobile';
import DocumnetTitle from 'react-document-title'
import ExchangeHeader from './subpage/ExchangeHeader'
import ExchangeForm from './subpage/ExchangeForm'
import ExchangePwd from './subpage/ExchangePwd'
import {AjaxSend,getCookie,to_fixed4,to_fixed2} from '../../../util/util'
const aler = Modal.alert;
var isset=false

@inject('rootStore')
@observer
class Exchange extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tab: ['',''],
      // tab_index:0,
      tab_index:getCookie('can_trade_oec')==1?0:1,
      show:false,
      pwd_num:'',
      orderPrice:'',//
      orderQty:'',//
      quotPrice:'',//
      openPrice:'',//
      hui_balance:'',//
      OEC_balance:'',//OEC
      price:'',
      num:'',
      fall:'',//
      rise:'',//
      plus:'',//
      exchange_fee:0,///
      real_score:0,//(oec/)
    };
  }

  componentDidMount() {
    console.log(this.props.rootStore.RouterName.RouterName)
    if(this.props.rootStore.RouterName.RouterName=='transfer'){
      this.setState({
        tab_index:1
      })
    }else{

    }
    this.get_data()
  }
  componentWillUnmount(){
    this.props.rootStore.RouterName.changeRouterName('dealExchange')
  }
  tab_bind(index){
    this.setState({
      tab_index:index,
      price:'',
      num:'',
      exchange_fee:0,
      real_score:0,
    })
  }
  show_pwd(e){
    this.setState({
      show:e
    })
  }
  pwd_num(e){
    this.setState({
      pwd_num:e
    })
  }

  get_data(){

    var _this = this
    AjaxSend({_cmd: 'oec_quot_getQutot'}, (da) => {
      _this.setState({
        openPrice:to_fixed4(da.data.openPrice),
        quotPrice:to_fixed4(da.data.quotPrice),
      });
    }, (errors) => {
      alert(errors);
    })
//
    AjaxSend({_cmd: 'oec_user_account_getbalance',
      currency:2}, (da) => {
      _this.setState({
        hui_balance:to_fixed4(da.data[1].balance)
      });
    }, (errors) => {
      alert(errors);
    })
    //OEC
    AjaxSend({_cmd: 'oec_user_account_getbalance',
      currency:102}, (da) => {
      _this.setState({
        OEC_balance:to_fixed4(da.data[102].balance)
      });
    }, (errors) => {
      alert(errors);
    })
    AjaxSend({_cmd: 'oec_user_order_getRangeData'},(res)=> {
      _this.setState({
        fall:res.data.fall,
        rise:res.data.rise,
        plus:res.data.plus,
      })}, (error)=> {
      Toast.error(error)
    });
  }
  exchange(type){
    // var can_trade_oec = getCookie('can_trade_oec')
    // if(can_trade_oec==0){
    //   Toast.info('', 2)
    //   return false
    // }
    var _this = this;
    var fall = '';
    var rise = '';
    var plus = 0;
    var price = parseFloat(this.state.price);
    var num = parseFloat(this.state.num);
    var openPrice = parseFloat(this.state.openPrice);
    var quotPrice = parseFloat(this.state.orderPrice);
    var hui_balance = this.state.hui_balance;
    var OEC_balance = parseFloat(this.state.OEC_balance);
    console.log(price)
    if(this.state.price==''||price==NaN){
      if(type==1){
        Toast.info('',2)
      }else{
        Toast.info('',2)
      }
      return
    }
    if(this.state.num==''||num==NaN||num==0){
      if(type==1){
        Toast.info('0')
      }else{
        Toast.info('0')
      }
      return
    }
    if(hui_balance<0.01){
      if(type==1){
        Toast.info('')
        return
      }
    }
    if(OEC_balance<0.01){
      if(type==2){
        Toast.info('OEC')
        return
      }
    }


      fall = parseFloat(_this.state.fall);
      rise = parseFloat(_this.state.rise);
      plus = parseFloat(_this.state.plus);
      if(type==1){
          if(getCookie('can_trade_oec') == 1){
            console.log(',')
          }else{
            if (price < (openPrice * fall)) {
              Toast.info('' + to_fixed4(openPrice * fall))
              return
            }
            if (price > (openPrice + (openPrice * plus))) {
              Toast.info('' + to_fixed4(openPrice + (openPrice * plus)))
              return
            }
            if (price > (openPrice * rise)) {
              Toast.info('' + to_fixed4(openPrice * rise))
              return
            }
          }
        if(num>(hui_balance/price)){
          Toast.info(''+to_fixed2(hui_balance/price)+'OEC')
          return
        }
        _this.validatePayPwd()
      }
      else{

          if(getCookie('can_trade_oec') == 1){
            console.log(',')
          }else{
            if (price < (openPrice * fall)) {
              Toast.info('' + to_fixed4(openPrice * fall))
              return
            }

            if (price > (openPrice + (openPrice * plus))) {
              Toast.info('' + to_fixed4(openPrice + (openPrice * plus)))
              return
            }
            if (price > (openPrice * rise)) {
              Toast.info('' + to_fixed4(openPrice * rise))
              return
            }
          }
        if(num>OEC_balance){
          Toast.info(''+to_fixed2(OEC_balance)+'OEC')
          return
        }
        _this.validatePayPwd()
      }
    }
  validatePayPwd() {
    var _this = this;
    var data = {
      _cmd: 'usercenter_exists_paypwd'
    }
    AjaxSend(data,(da)=> {
      if (da.data.isset != 1) {
        isset=false
        aler('', '', [
          { text: '', onPress: () =>  window.location.href = "/trader_password.php", style: { fontWeight: 'bold' } },
        ])
        return
      }else{
        isset=true
        _this.setState({
          show:true
        })

      }

    }, (error)=> {
    });
  }
  set_price(e){
    this.setState({
      price:e
    })
    if(e==''||this.state.num==''){
      this.setState({
        exchange_fee:0,
      })
    }else{
      if(this.state.tab_index==0){
        this.setState({
          exchange_fee:to_fixed4(e*this.state.num*0.01),
          real_score:to_fixed4(((e*this.state.num)-(e*this.state.num*0.01))/e),
        })
      }else{
        this.setState({
          exchange_fee:to_fixed4(this.state.price*this.state.num*0.01),
          real_score:to_fixed4((this.state.price*this.state.num)-(this.state.price*this.state.num*0.01)),
        })
      }

    }
  }
  set_num(e){
    this.setState({
      num:e
    })
    if(e==''||this.state.price==''){
      this.setState({
        exchange_fee:0,
      })
    }else{
      if(this.state.tab_index==0){
        this.setState({
          exchange_fee:to_fixed4(e*this.state.price*0.01),
          real_score:to_fixed4(((e*this.state.price)-(e*this.state.price*0.01))/this.state.price),
        })
      }else{
        this.setState({
          exchange_fee:to_fixed4(this.state.price*e*0.01),
          real_score:to_fixed4((this.state.price*e)-(this.state.price*e*0.01)),
        })
      }

    }
  }
  show_rules(e){
    var plus =parseFloat(this.state.plus);
    const Content_1=()=>(
       <div className="modal_content_1">
        <p>1{plus}1010+{plus}={10+plus}</p>
        <p>20.01</p>
      </div>
    )
    const Content_2=()=>(
      <div className="modal_content_1">
        <p>1{plus}1010+{plus}={10+plus}</p>
        <p>20.01</p>
      </div>
    )
    if(e==1){
      aler('', <Content_1 />, [
        { text: '', onPress: () => {} },
      ])
    }else{
      aler('', <Content_2/>, [
        { text: '', onPress: () => {} },
      ])
    }

  }
  componentWillReceiveProps(nextProps) {}


  render() {
    return (
      <div className="deal_exchange">

        {this.state.show? <ExchangePwd show_pwd={this.show_pwd.bind(this)} pwd_num={this.pwd_num.bind(this)}
                                       type={this.state.tab_index+1} price={this.state.price} num={this.state.num}
        />:''}
        <div className="exchange_header flex-column">
          <div className="tab flex-row-around">
            {getCookie('can_trade_oec')==1?this.state.tab.map((item,index)=>(
              <div style={{borderRadius:index==0?'.18rem 0 0 .18rem':'0 .18rem .18rem 0'}} className={index==this.state.tab_index?"tab_item tab_item_act":"tab_item"} onClick={this.tab_bind.bind(this,index)} key={index}>{item}</div>
            )):<div className="tab_item tab_item_act" style={{borderRadius:'.18rem .18rem'}}>{(this.state.tab)[1]}</div>}
          </div>
          <ExchangeHeader type={this.state.tab_index} quotPrice={this.state.quotPrice} hui_balance={this.state.hui_balance} OEC_balance={this.state.OEC_balance}/>
        </div>
        <ExchangeForm type={this.state.tab_index} quotPrice={this.state.quotPrice} hui_balance={this.state.hui_balance} OEC_balance={this.state.OEC_balance} price={this.state.price} num={this.state.num} set_price={this.set_price.bind(this)} set_num={this.set_num.bind(this)}/>
        <div className="ex_rules flex-row-between">
          <div className="ex_rules_left">
            {this.state.tab_index==0?<div className="ex_rules_left1">
              1%{this.state.exchange_fee}<br/>
              OEC: {this.state.num}<br/>
              : {to_fixed4(parseFloat(this.state.num*this.state.price)+parseFloat(this.state.exchange_fee))}
            </div>:
            <div className="ex_rules_left1">
              1%{this.state.exchange_fee}<br/>
              {this.state.real_score}
            </div>}
          </div>
          {this.state.tab_index==0?<div className="ex_rules_right" onClick={this.show_rules.bind(this,1)}></div>:
          <div className="ex_rules_right" onClick={this.show_rules.bind(this,2)}></div>}
        </div>
        {this.state.tab_index==0?
          <div className="commit_btn" onClick={this.exchange.bind(this,1)}></div>:
          <div className="commit_btn" onClick={this.exchange.bind(this,2)}></div>}
          {this.state.tab_index==0?
            <DocumnetTitle title=""/>:
            <DocumnetTitle title=""/>}
      </div>
    )
  }
}

export default Exchange



